.slider * { box-sizing: border-box; }

.slider__wrap { position: relative; }

.slider__controls { position: absolute; z-index: 1; top: 50%; left: 0; transform: translate(0, -50%); width: 100%; }

.slider__prev, .slider__next { display: flex; justify-content: center; align-items: center; background-color: #fff; border-radius: 50%; width: 4.0625rem; height: 4.0625rem; position: absolute; top: 50%; }

.slider__prev .prev, .slider__next .prev { margin-left: 1.2em; }

.slider__prev .next, .slider__next .next { margin-right: 1.2em; }

.slider__prev.swiper-button-disabled, .slider__next.swiper-button-disabled { opacity: .5; pointer-events: none; }

.slider__prev { left: 0; transform: translate(-50%, -50%); }

.slider__next { right: 0; transform: translate(50%, -50%); }

.overflow-hidden { overflow: hidden; }

.pt-0 { padding-top: 0 !important; }

.pb-0 { padding-bottom: 0 !important; }

.text-left { text-align: left; }

